	var XMLJardimMedicinal = '<?xml version="1.0" encoding="UTF-8"?>\n<Unscramble>\n\t<fase1>\n\t\t<Conteudo1>\n\t\t\t<Dica>Nome popular da Mentha pulegium, planta que recebeu seu nome devido a sua capacidade de afastar pulgas.</Dica>\n\t\t\t<PalavraAssoc>POEJO</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>O poejo, possui propriedades medicinais auxiliando no tratamento dos mais diversos tipos de doen\xE7as relacionadas \xE0 ______.</Dica>\n\t\t\t<PalavraAssoc>PELE</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>Principal componente qu\xEDmico encontrado no Capim-lim\xE3o, muito utilizado na ind\xFAstria de cosm\xE9ticos.</Dica>\n\t\t\t<PalavraAssoc>CITRAL</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecida a Erva-doce.</Dica>\n\t\t\t<PalavraAssoc>ANIS</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>Nome popular da Malva sylvestris L, planta que possui flores azuis, roxas ou p\xFArpuras, com p\xE9talas grandes, tr\xEAs vezes maiores que o c\xE1lice.</Dica>\n\t\t\t<PalavraAssoc>MALVA</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>O xarope desta planta \xE9 extremamente popular no Brasil para o tratamento de tosses e outras doen\xE7as respirat\xF3rias.</Dica>\n\t\t\t<PalavraAssoc>GUACO</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>O guaco, al\xE9m de ser utilizado como xarope para o tratamento de tosses e outras doen\xE7as, \xE9 utilizado no tratamento contra o veneno das picadas de _____.</Dica>\n\t\t\t<PalavraAssoc>COBRA</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>Planta com propriedades digestivas e aperitivas, sua a\xE7\xE3o se d\xE1 pelo est\xEDmulo \xE0 saliva\xE7\xE3o e \xE0 produ\xE7\xE3o de sucos g\xE1stricos.</Dica>\n\t\t\t<PalavraAssoc>LOSNA</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>O ____  extra\xEDdo da arruda n\xE3o deve ser ingerido, mas pode ser utilizado nos cuidados sobre a pele.</Dica>\n\t\t\t<PalavraAssoc>CH\xC1</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>A arruda,  conhecida como \u201Cerva purificadora\u201D usada na crendice popular para espantar ____ olhados e energias negativas.</Dica>\n\t\t\t<PalavraAssoc>MAUS</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>O Alecrim, \xE9 um ingrediente muito comum em cosm\xE9ticos e quando usado na ____, ajuda no fortalecimento dos vasos capilares e do couro cabeludo.</Dica>\n\t\t\t<PalavraAssoc>PELE</PalavraAssoc>\n\t\t</Conteudo1>\n\t\t<Conteudo1>\n\t\t\t<Dica>A C\xE2nfora, \xE9 uma planta que  devido suas propriedades medicinais \xE9 muito utilizada na fabrica\xE7\xE3o de talco para os _____.</Dica>\n\t\t\t<PalavraAssoc>P\xC9S</PalavraAssoc>\n\t\t</Conteudo1>\t\t\n\t</fase1>\n\t<fase2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Nome popular da Salvia officinalis, planta que possui folhas aos pares, verde-acinzentadas e flores, conforme a variedade, de colora\xE7\xE3o azul, violeta, rosa ou branca.</Dica>\n\t\t\t<PalavraAssoc>S\xC1LVIA</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>O Alecrim, na Antiguidade era usada como m\xE9todo de preserva\xE7\xE3o do ______ dos mortos.</Dica>\n\t\t\t<PalavraAssoc>CORPO</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>A Arruda, \xE9 um ingrediente usado na produ\xE7\xE3o da bebida italiana Candolini Grappa _____ e em outras bebidas alco\xF3licas.</Dica>\n\t\t\t<PalavraAssoc>RUTA</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Nome popular da Ruta graveolens L, planta que possui muitos ramos com folhas ovais, pequenas e carnudas, de cor verde-acinzentada e flores pequenas e amarelas.</Dica>\n\t\t\t<PalavraAssoc>ARRUDA</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Nome popular da Rosmarinus officinalis, planta que possui folhas lineares com uma colora\xE7\xE3o verde na parte superior e mais esbranqui\xE7ada na parte inferior.</Dica>\n\t\t\t<PalavraAssoc>ALECRIM</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Na Antiguidade, com uso da Arruda, as pessoas acreditavam que poderia combater qualquer tipo de doen\xE7a, inclusive a Peste ______.</Dica>\n\t\t\t<PalavraAssoc>NEGRA</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>O consumo da planta Marcela \xE9 recomendado para pessoas que consomem muito _____, pois ajuda a diminuir consideravelmente os n\xEDveis de cafe\xEDna no sangue.</Dica>\n\t\t\t<PalavraAssoc>CAF\xC9</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Planta que devido \xE0s suas propriedades alucin\xF3genas foi proibida em diversos pa\xEDses, principalmente se o seu consumo for em forma de licor.</Dica>\n\t\t\t<PalavraAssoc>ABSINTO</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>A Hortel\xE3 \xE9 considerada como principal ingrediente na ind\xFAstria que produz produtos para a higiene bucal, como os ______ dentais.</Dica>\n\t\t\t<PalavraAssoc>CREMES</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>A Lavanda \xE9  utilizada na fabrica\xE7\xE3o de produtos para a higiene pessoal,principalmente para  o ______.</Dica>\n\t\t\t<PalavraAssoc>BANHO</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>Nome popular da Passiflora edulis, conhecida como Maracuj\xE1,  tamb\xE9m \xE9  conhecida como flor da _____.</Dica>\n\t\t\t<PalavraAssoc>PAIX\xC3O</PalavraAssoc>\n\t\t</Conteudo2>\n\t\t<Conteudo2>\n\t\t\t<Dica>A cavalinha j\xE1 chegou a possuir mais de 12 metros de _____ no per\xEDodo em que a Terra concentrava maiores taxas de carbono.</Dica>\n\t\t\t<PalavraAssoc>ALTURA</PalavraAssoc>\n\t\t</Conteudo2>\t\t\n\t</fase2>\n\t<fase3>\n\t\t<Conteudo3>\n\t\t\t<Dica>A malva, devido \xE0 suas propriedades anti-inflamat\xF3rias \xE9 comumente utilizada no combate a dores de _________.</Dica>\n\t\t\t<PalavraAssoc>GARGANTA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Nome popular da Mentha viridis, planta que possui folhas de aroma bem caracter\xEDstico e que raramente floresce. \xC9 muito utilizada como verm\xEDfugo.</Dica>\n\t\t\t<PalavraAssoc>LEVANTE</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Nome popular da Achyrocline satureioides, planta que possui folhas alongadas, finas, com muitos pelos revestido e de colora\xE7\xE3o amarelada.</Dica>\n\t\t\t<PalavraAssoc>MARCELA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Planta que recebeu este nome em homenagem a ninfa grega, Mellona, a protetora das abelhas.</Dica>\n\t\t\t<PalavraAssoc>MELISSA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>A origem desta planta homenageia a ninfa Menthe da mitologia grega. E \xE9 usada hoje em dia na fabrica\xE7\xE3o de diversos produtos, principalmente aqueles voltados para a higiene bucal.</Dica>\n\t\t\t<PalavraAssoc>HORTEL\xC3</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Palavra do latim cujo significado \xE9 \u201Clavar\u201D que d\xE1 origem ao nome Lavanda.</Dica>\n\t\t\t<PalavraAssoc>LAVARE</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>O seu efeito _______ \xE9 a principal propriedade medicinal conhecida da Melissa.</Dica>\n\t\t\t<PalavraAssoc>CALMANTE</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>O Absinto \xE9 usado como ingrediente principal de um ______ muito apreciado na Europa do s\xE9culo XIX.</Dica>\n\t\t\t<PalavraAssoc>DESTILADO</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>O ch\xE1 desta planta \xE9 bom para quem possui diabetes j\xE1 que ele reduz a concentra\xE7\xE3o de a\xE7\xFAcar no sangue.</Dica>\n\t\t\t<PalavraAssoc>CARQUEJA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Nome popular da Lavandula officinalis, planta que possui flores de cor azul ou violeta-lil\xE1s, pequenas e dispostas numa espiga.</Dica>\n\t\t\t<PalavraAssoc>LAVANDA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Nome popular da Artemisia camphorata, erva rasteira, de folhas profundamente recortadas, verde-acinzentadas.</Dica>\n\t\t\t<PalavraAssoc>C\xC2NFORA</PalavraAssoc>\n\t\t</Conteudo3>\n\t\t<Conteudo3>\n\t\t\t<Dica>Losna ou ______ \xE9 como popularmente ficou conhecida a Artemisia absinthium, nome que se popularizou ap\xF3s o seu alto consumo na forma de licor na Europa de XIX.</Dica>\n\t\t\t<PalavraAssoc>ABSINTO</PalavraAssoc>\n\t\t</Conteudo3>\t\t\n\t</fase3>\n\t<fase4>\n\t\t<Conteudo4>\n\t\t\t<Dica>A S\xE1lvia, devido a uma de suas propriedades medicinais j\xE1 foi utilizada para tratar um dos efeitos do Mal de ________.</Dica>\n\t\t\t<PalavraAssoc>PARKINSON</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecido o Manjeric\xE3o.</Dica>\n\t\t\t<PalavraAssoc>ALFAVACA</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Devido suas caracter\xEDsticas morfol\xF3gicas peculiares, seu nome foi dado, pois remete a um rabo de cavalo. Um dos componentes em sua composi\xE7\xE3o fortalece os tecidos conjuntivos do corpo e beneficia pacientes no tratamento de artrite.</Dica>\n\t\t\t<PalavraAssoc>CAVALINHA</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecida a Erva-cidreira.</Dica>\n\t\t\t<PalavraAssoc>CITRONELA</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>A malva, na crendice popular afasta os maus ________.</Dica>\n\t\t\t<PalavraAssoc>ESP\xCDRITOS</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecida a Lavanda.</Dica>\n\t\t\t<PalavraAssoc>ALFAZEMA</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Planta administrada em forma de extratos ou suplementos para o tratamento de doen\xE7as que agem no sistema nervoso, incluindo depress\xE3o, ansiedade e ataques de p\xE2nico.</Dica>\n\t\t\t<PalavraAssoc>MARACUJ\xC1</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Planta medicinal popular no Brasil utilizada no tratamento da ins\xF4nia devido suas propriedades sedativas.</Dica>\n\t\t\t<PalavraAssoc>CAMOMILA</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Na culin\xE1ria, \xE9 muito utilizado em ensopados, saladas, temperos para carnes e peixes e como acr\xE9scimo em molhos (principalmente o de tomate).</Dica>\n\t\t\t<PalavraAssoc>MANJERIC\xC3O</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>O ch\xE1 extra\xEDdo da erva-cidreira traz _________ para o sistema nervoso.</Dica>\n\t\t\t<PalavraAssoc>BENEF\xCDCIOS</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>A ingest\xE3o do ch\xE1 de Cavalinha traz benef\xEDcios para os tecidos __________ do corpo.</Dica>\n\t\t\t<PalavraAssoc>CONJUNTIVOS</PalavraAssoc>\n\t\t</Conteudo4>\n\t\t<Conteudo4>\n\t\t\t<Dica>Nome popular da Sedum dendroideum, planta que possui folhas carnosas, lisas e brilhantes, de formato espatulado a oval, de cor verde a bronzeada.</Dica>\n\t\t\t<PalavraAssoc>B\xC1LSAMO</PalavraAssoc>\n\t\t</Conteudo4>\n\t</fase4>\n\t<fase5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecida a Cavalinha.</Dica>\n\t\t\t<PalavraAssoc>ERVA-CANUDO</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecido o Guaco.</Dica>\n\t\t\t<PalavraAssoc>CIP\xD3 DE COBRA</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Outro nome pelo qual \xE9 conhecida a planta Melissa.</Dica>\n\t\t\t<PalavraAssoc>ERVA-CIDREIRA</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Planta utilizada no preparo do \u201Cch\xE1 da estrada\u201D. E que devido ao seu aroma acaba sendo muito confundida com a Melissa.</Dica>\n\t\t\t<PalavraAssoc>CAPIM LIM\xC3O</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Outro nome pelo qual tamb\xE9m \xE9 conhecido o Maracuj\xE1.</Dica>\n\t\t\t<PalavraAssoc>FLOR DA PAIX\xC3O</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Nome popular dado ao destilado produzido a partir da planta Artemisia absinthium.</Dica>\n\t\t\t<PalavraAssoc>FADA VERDE</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Planta que auxilia no tratamento de pessoas hipertensas.</Dica>\n\t\t\t<PalavraAssoc>ERVA-CIDREIRA</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Nome popular da Pimpinella anisum L, planta que possui flores pequenas na forma de buqu\xEAs brancos com pequenos frutos secos de sabor adocicado e com cheiro forte.</Dica>\n\t\t\t<PalavraAssoc>ERVA-DOCE</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Nome cient\xEDfico da Cymbopogon _______, conhecido como Capim Lim\xE3o, possui folhas longas, com bordas cortantes e de colora\xE7\xE3o verde clara, exalando um forte odor de lim\xE3o.</Dica>\n\t\t\t<PalavraAssoc>CITRATUS</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>A S\xE1lvia \xE9 popularmente conhecida por ser a \u201Cplanta da __________\u201D.</Dica>\n\t\t\t<PalavraAssoc>LONGEVIDADE</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>O Alecrim \xE9 considerado um aliado no ________.</Dica>\n\t\t\t<PalavraAssoc>EMAGRECIMENTO</PalavraAssoc>\n\t\t</Conteudo5>\n\t\t<Conteudo5>\n\t\t\t<Dica>Nome cient\xEDfico da __________ officinalis, conhecida como Lavanda</Dica>\n\t\t\t<PalavraAssoc>LAVANDULA</PalavraAssoc>\n\t\t</Conteudo5>\n\t</fase5>\n</Unscramble>';
	
